import React, { Component } from 'react'
import axios from 'axios'
import _ from 'lodash'
// import Chart from 'chart.js'

export default class Dashboard extends Component {
    constructor(props) {
      super(props);
      this.state = {
        faq: [],
        totals: {},
        nepalTotals: {},
        nepalData: {},
        top5word: []
      }
    }
    componentDidMount = async() => {
      const config = {
        headers: {
          "content-type": "application/octet-stream",
          "x-rapidapi-host": "covid-19-data.p.rapidapi.com",
          "x-rapidapi-key": "1a6e601fbdmsh640d4e15ccf426bp16342djsn6886e3aebe85"
        }
      };

      const faq = await axios.get('https://nepalcorona.info/api/v1/faqs')
      const totals = await axios.get('https://nepalcorona.info/api/v1/data/world')
      const nepalData = await axios.get('https://nepalcorona.info/api/v1/data/nepal')
      this.setState({
        faq: faq.data.data,
        totals: totals.data[0],
        nepalTotals: totals.data.find((data) => data.country == 'Nepal'),
        nepalData: nepalData.data,
        top5word: _.tail(totals.data).slice(1, 6)
      })
    }

    getPercentage = (total, num) => {
      return (num / total) * 100
    }
    render() {
      const {totals, nepalTotals, nepalData, top5word} = this.state
      console.log('this.state,,,,,,', nepalData, top5word)

      
        return (
            <div>
           <div className="right_col" role="main">
            {/* top tiles */}
            <div className="row tile_count">
              <div className="col-md-2 col-sm-4 col-xs-6 tile_stats_count">
                <span className="count_top"><i className="fa fa-user" /> Total Cases</span>
                  <div className="count">{totals.totalCases && totals.totalCases.toLocaleString()}</div>
              </div>
              <div className="col-md-2 col-sm-4 col-xs-6 tile_stats_count">
                <span className="count_top"><i className="fa fa-user" /> Total Deaths</span>
                <div className="count red">{totals.totalDeaths && totals.totalDeaths.toLocaleString()}</div>
              </div>
              <div className="col-md-2 col-sm-4 col-xs-6 tile_stats_count">
                <span className="count_top"><i className="fa fa-user" /> Total Recovered</span>
                  <div className="count green">{totals.totalRecovered && totals.totalRecovered.toLocaleString()}</div>
              </div>
              <div className="col-md-2 col-sm-4 col-xs-6 tile_stats_count">
                <span className="count_top"><i className="fa fa-user" /> Active Cases</span>
                  <div className="count blue">{totals.activeCases && totals.activeCases.toLocaleString()}</div>
              </div>
              <div className="col-md-2 col-sm-4 col-xs-6 tile_stats_count">
                <span className="count_top"><i className="fa fa-user" /> Nepal Cases</span>
                <div className="count">{nepalTotals.totalCases && nepalTotals.totalCases.toLocaleString()}</div>
              </div>
              <div className="col-md-2 col-sm-4 col-xs-6 tile_stats_count">
                <span className="count_top"><i className="fa fa-user" /> Recovered</span>
                <div className="count green">{nepalTotals.totalRecovered && nepalTotals.totalRecovered.toLocaleString()}</div>
              </div>
            </div>
            {/* /top tiles */}

            <div className="row">
              <div className="col-md-12 col-sm-12 col-xs-12">
                <div className="x_panel">
                  <div className="x_title">
                    <h2>Total COVID 19 infected density</h2>
                    <div className="clearfix" />
                  </div>
                  <div className="x_content">
                    <div className="dashboard-widget-content">
                      <div className="col-md-4 hidden-small">
                        {/* <h2 className="line_30">125.7k Views from 60 countries</h2> */}
                        <table className="countries_list">
                          <tbody>
                              {top5word.map((data) => 
                                <tr>
                                  <td>{data.country}</td>
                                  <td className="fs15 fw700 text-right">{data.totalCases.toLocaleString()}</td>
                                </tr>
                              )}
                          </tbody>
                        </table>
                      </div>
                      <div id="world-map-gdp" className="col-md-8 col-sm-12 col-xs-12" style={{ height: '230px' }} />
                    </div>
                  </div>
                </div>
              </div>
            </div>
            
            <br />
            <div className="row">
              <div className="col-md-6 col-sm-6 col-xs-12">
                <div className="x_panel tile fixed_height_320">
                  <div className="x_title">
                    <h2>Nepal's current data</h2>
                    <div className="clearfix" />
                  </div>
                  <div className="x_content">
                    <h4>Total tested: {nepalData.tested_total}</h4>
                    <br/>
                    <div className="widget_summary">
                      <div className="w_left w_25">
                        <span>Tested positive</span>
                      </div>
                      <div className="w_center w_55">
                        <div className="progress">
                            <div className="progress-bar bg-green" role="progressbar" aria-valuenow={60} aria-valuemin={0} aria-valuemax={100} style={{ width: `${this.getPercentage(nepalData.tested_total, nepalData.tested_positive)}%`}}>
                            {/* <span className="sr-only">60% Complete</span> */}
                          </div>
                        </div>
                      </div>
                      <div className="w_right w_20">
                        <span>{nepalData.tested_positive}</span>
                      </div>
                      <div className="clearfix" />
                    </div>
                    <div className="widget_summary">
                      <div className="w_left w_25">
                        <span>Tested negative</span>
                      </div>
                      <div className="w_center w_55">
                        <div className="progress">
                          <div className="progress-bar bg-green" role="progressbar" aria-valuenow={60} aria-valuemin={0} aria-valuemax={100} style={{width: `${this.getPercentage(nepalData.tested_total, nepalData.tested_negative)}%`}}>
                            {/* <span className="sr-only">60% Complete</span> */}
                          </div>
                        </div>
                      </div>
                      <div className="w_right w_20">
                        <span>{nepalData.tested_negative}</span>
                      </div>
                      <div className="clearfix" />
                    </div>
                    <div className="widget_summary">
                      <div className="w_left w_25">
                        <span>Deaths</span>
                      </div>
                      <div className="w_center w_55">
                        <div className="progress">
                          <div className="progress-bar bg-green" role="progressbar" aria-valuenow={60} aria-valuemin={0} aria-valuemax={100} style={{width: `${this.getPercentage(nepalData.tested_total, nepalData.deaths)}%`}}>
                            {/* <span className="sr-only">60% Complete</span> */}
                          </div>
                        </div>
                      </div>
                      <div className="w_right w_20">
                        <span>{nepalData.deaths}</span>
                      </div>
                      <div className="clearfix" />
                    </div>
                    <div className="widget_summary">
                      <div className="w_left w_25">
                        <span>In isolation</span>
                      </div>
                      <div className="w_center w_55">
                        <div className="progress">
                            <div className="progress-bar bg-green" role="progressbar" aria-valuenow={60} aria-valuemin={0} aria-valuemax={100} style={{ width: `${this.getPercentage(nepalData.tested_total, nepalData.in_isolation)}%`}}>
                          </div>
                        </div>
                      </div>
                      <div className="w_right w_20">
                        <span>{nepalData.in_isolation}</span>
                      </div>
                      <div className="clearfix" />
                    </div>
                  </div>
                </div>
              </div>
              <div className="col-md-6 col-sm-6 col-xs-12">
                <div className="x_panel tile fixed_height_320 overflow_hidden">
                  <div className="x_title">
                    <h2>Recovery data</h2>
                    <div className="clearfix" />
                  </div>
                  <div className="x_content">
                    <table  style={{width: '100%'}}>
                      <tbody>
                        <tr>
                          <th>Country</th>
                          <th>Recovered</th>
                          <th>Death</th>
                        </tr>
                          {top5word.map((data) =>
                            <tr>
                              <td>
                                <i className="fa fa-square blue" /> {data.country}
                              </td>
                              <td>{Math.round(this.getPercentage(data.totalCases, data.totalRecovered))}%</td>
                              <td>{Math.round(this.getPercentage(data.totalCases, data.totalDeaths))}%</td>
                            </tr>
                          )}
                          
                      </tbody></table>
                  </div>
                </div>
              </div>
            </div>
            <div className="row">
              <div className="col-md-12 col-sm-12 col-xs-12">
                <div className="x_panel">
                  <div className="x_title">
                    <h2>Frequently asked questions</h2>
                    
                    <div className="clearfix" />
                  </div>
                  <div className="x_content">
                    <div className="dashboard-widget-content scroll">
                      <ul className="list-unstyled timeline widget">
                        {this.state.faq.map((faq) => 
                          <li>
                            <div className="block">
                              <div className="block_content">
                                <h2 className="title">
                                  <a>{faq.question}</a>
                                </h2>
                                <div className="byline">
                                  <span>13 hours ago</span>
                                  <span>Category:</span> <a>{faq.category}</a>
                                </div>
                                <p className="excerpt">{faq.answer}
                                </p>
                              </div>
                            </div>
                          </li>
                        )}
                      </ul>
                    </div>
                  </div>
                </div>
              </div>
          </div>
                         
            </div>
          </div>
        )
    }
}
